:root{
  --red: #bb3535;
  --green: #4aec8c;
}

body{
  background-image: url('https://64.media.tumblr.com/949189283a218fe83bacb76e461906ba/aff29a0db74fcca6-9b/s640x960/53144fc7f67d38278601ca38fa15fac04779d654.gifv');
  background-repeat: no-repeat;
  background-size: cover;
  color: #eee;
}
main{
  padding-top: 50px;
  max-width: 340px;
  margin: 0 auto;
  text-align: center;
}

button{
  background-color: transparent;
  color: rgba(255,255,255,0.8);
  border:0;
  display: inline-block;
  width: 100px;
  cursor: pointer;
}

button.with-text{
  width: auto;
  background-color:rgba(255,255,255,0.1);
  border-radius:10px;
  padding: 10px 20px;
  font-size: 1.4rem;
  line-height: 36px;
}
button.with-text svg{
  width: 36px;
  float:left;
  margin-right: 5px;
}

label{
  display:block;
  margin-bottom: 10px;
  margin-top: 20px;
  text-transform: capitalize;
}